.Red {
    color: red;
  }
  
.Bold {
    font-weight: bold;  
}

.Button {
    border: 1px solid lightblue;
    padding: 16px;
    background-color: greenyellow;
    font: inherit;
    font-weight: 600;
    color: black;
    cursor: pointer;
}

.Button:hover {
    font-weight: 600;
    color: yellow;
    background-color: darkorange;
}

.Button.Red {
    background-color: deeppink;  
}

.Button.Red:hover {  
    background-color: salmon;
    color: yellow;
}


