.Person {
    width: 60%;
    margin: 16px auto;
    border: 1px solid #eeee;
    -webkit-box-shadow: 0 2px 3px #ccc;
            box-shadow: 0 2px 3px #ccc;
    padding: 16px;
    text-align: center;
}

.Button {
    color: powderblue;
    background-color: dimgray;
    border: 1px solid darkorange;
    padding: 8px;
    font: inherit;
    font-weight: 400;
}

/* if object is more then 500px it resized to 450px */
@media (min-width: 500px) {
    .Person {
        width: 450px;
    }
}



